import React,{Component} from 'react';
import store from '../store.js';
import Goods from './Goods.js';
import Comment from './Comment.js';
import Shop from './Shop.js';

class ShopPageMain extends Component{
	constructor(){
		super();
		this.state={
			showChildTab:<Goods obj={store.getState()} />,
			current:0,
		}
		this.tabPage = this.tabPage.bind(this);
	}
	tabPage(e){
//		console.log(e.target.dataset.num);
		this.setState({
			current:e.target.dataset.num,
		})
		switch(e.target.dataset.num){
			case '0':
			this.setState({
		
				showChildTab:<Goods obj={store.getState()} />
			});
			break;
			case '1':
			this.setState({
				showChildTab:<Comment  />
			});
			break;
			case '2':
			this.setState({
				showChildTab:<Shop />
			});
			break;
		}
		
		
		
	}
	
	render(){
		//在shopPageBuyCar 加减时，可以同时改变goods里面的值
		var index = this.state.current;
		if(index == 0){
			this.state.showChildTab=<Goods obj={store.getState()} />;
		}
		
		
		return (
			<div>
				<div className='shopPageMaintab'>
					<span className={index == 0?'active':''} data-num='0' onClick={this.tabPage}>商品</span>
					<span className={index == 1?'active':''} data-num='1' onClick={this.tabPage}>评价</span>
					<span className={index == 2?'active':''} data-num='2' onClick={this.tabPage}>店铺</span>
				</div>
				{this.state.showChildTab}
				
				
				
				
			</div>
			
		)
	}
}


export default ShopPageMain;



