import React,{Component} from 'react';
import store from './store.js';
import data from './data.js'

class Takeoutfood extends Component {
	constructor(){
		super();
		this.state={
			showObj:data.listPage,
		}
	}
	goPage(){
//		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	render(){
		return (
			<div className="homepage"  >
				
				<div>
					<div className="hometitle">
						<div className="titletop">
							<div className="pos">
								<span>广州</span>
							</div>
							<div className="weather">
								<div>
									<span>18℃ </span>
									<span> 晴</span>
								</div>
								<div></div>
							</div>
						</div>
						<div className="search">
							<span>搜索商家 商品名称</span>
						</div>
						<div className="titlebottom">
							<ul>
								<li>米粉</li>
								<li>小龙虾</li>
								<li>鱼粉</li>
								<li>炸鸡</li>
								<li>干锅</li>
								<li>麦当劳</li>
								<li>烧鸡</li>
								<li>小笼包</li>
							</ul>
						</div>
						
					</div>
					<div className="homepagebox"></div>
					<div className="homemain">
					<div className="homemain1" onClick = {this.goPage}>
						<ul>
						   <li >
							   	<img src={'//fuss10.elemecdn.com/b/7e/d1890cf73ae6f2adb97caa39de7fcjpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/'} />
							   	<span>美食</span>
						   </li>
						    <li>
							   	<img src={"//fuss10.elemecdn.com/2/35/696aa5cf9820adada9b11a3d14bf5jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"}/>
							   	<span>甜品饮品</span>
						   </li>
						    <li>
							   	<img src={'//fuss10.elemecdn.com/c/3c/0184f5b3fa72f295fc01864734218jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/'}/>
							   	<span>商超便利</span>
						   </li>
						    <li>
							   	<img src={"//fuss10.elemecdn.com/c/db/d20d49e5029281b9b73db1c5ec6f9jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"}/>
							   	<span>果蔬生鲜</span>
						   </li>
						    <li>
							   	<img src={"//fuss10.elemecdn.com/a/fa/d41b04d520d445dc5de42dae9a384jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"}/>
							   	<span>新品特惠</span>
						   </li>
						    <li>
							   	<img src={"//fuss10.elemecdn.com/6/74/785eafaf358fa6b18df37c64c3510jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"}/>
							   	<span>品牌专区</span>
						   </li>
						    <li>
							   	<img src={"//fuss10.elemecdn.com/4/07/e6c5aa440aa33e1e936ff1b6dae0ejpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"}/>
							   	<span>夜宵</span>
						   </li>
						    <li>
							   	<img src={"//fuss10.elemecdn.com/b/e1/58aa34ef194d216c9f2328f603588jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"}/>
							   	<span>医药健康</span>
						   </li>
						   
						</ul>
					</div>
					
					<div className="homemain2">
						<div>
							<span>限量抢购</span>
							<span>超值美味 9.9元起</span>
							<span>9999人正在抢</span>
							<img src={"//fuss10.elemecdn.com/b/e1/0fa0ed514c093a7138b0b9a50d61fpng.png?imageMogr/format/webp/thumbnail/!240x160r/gravity/Center/crop/240x160/"}/>
						</div>
						<div>
							<span className="hotsell">热卖套餐</span>
							<span>销量最高,好评最多</span>
							<span className="selltop">TOP 100{'>'}</span>
							<img src={"//fuss10.elemecdn.com/d/d4/16ff085900d62b8d60fa7e9c6b65dpng.png?imageMogr/format/webp/thumbnail/!240x160r/gravity/Center/crop/240x160/"}/>
						</div>
					</div>
					<div className="homemain3">
						<ul>
							<li>
								<span>天天特价</span>
								<span>低至1折</span>
								<img src={'//fuss10.elemecdn.com/5/06/ddb537220b8c56d4ef6ae22718984png.png?imageMogr/format/webp/thumbnail/!232x154r/gravity/Center/crop/232x154/'} />
							</li>
							<li>
								<span>吃货狂欢</span>
								<span>随叫随到</span>
								<img src={'//fuss10.elemecdn.com/a/98/8010e87e876a7bf137597bf7fd324png.png?imageMogr/format/webp/thumbnail/!232x154r/gravity/Center/crop/232x154/'} />
							</li>
							<li>
								<span>品质优选</span>
								<span>尖货来袭</span>
								<img src={'//fuss10.elemecdn.com/a/b5/dcb5bff74242e06b3c2efee1cbffcpng.png?imageMogr/format/webp/thumbnail/!232x154r/gravity/Center/crop/232x154/'} />
							</li>
						</ul>
					</div>
					
					<div className="homebottom">
						<div className="homeline">推荐商家</div>
						{this.state.showObj.meishi.map(function(item,index,arr){
							return (
								<div className="listshop" onClick={this.goPage} data-num={index} key={index}>
								  <div>
									<div className="shopimg"><img src={item.showImg}/></div>
									
									<div className="listshoptop">
										<div className="shopname">
											<div>
												<div className="brand">品牌</div>
												<span>{item.name}</span>
											</div>
											<div className="align">
												<span>保</span>
												<span>票</span>
											</div>
										</div>
										<div className="shopcomment">
											<div>{item.star} 月售{item.salesVolumn}单</div>
											<div>{item.send}</div>
										</div>
										<div className="shopdetail">
											<div>
												<span>¥{item.startPrice}起送</span>
												<span>|</span>
												<span>配送费¥{item.sendPrice}</span>
											</div>
											<div>
												<span>1.10km</span>
												<span>|</span>
												<span>{item.arrivetime}分钟</span>
											</div>
										</div>
										<div className="shopgood">
											<span></span>
											<span>{item.comment}</span>
										</div>
										<div className="line"></div>
										<div className="listshopbottom">
											<div >
												<span></span>
												<span>{item.discount}</span>
											</div>
											<div >
												<span></span>
												<span>{item.firstdiscount}</span>
											</div>
										</div>
									
									</div>
									
									
								</div>
							</div>
							)
						},this)}
				
					</div>
					</div>
					
					
				</div>
			</div>
		)
	}
}

export default Takeoutfood;


