import React,{Component} from 'react';
import store from '../store.js';
//import ShopCar from './shopcar.js'
import data from '../data.js';
import Order from './order.js';


class ShopPageBuyCar extends Component{
	constructor(){
		super();
		
		this.state={
			carshow:false,
			buyCarArr:0,
			showObj:data.listPage.meishi[store.getState().shopNum],
			total:0,
			lowprice:0,
			
			totalricecount:0
		}
//		this.state.goodsArr = data.listPage.meishi[0].showPage.goods.goodsArr[0];
//		this.state.goodsArr=this.state.showObj.showPage.goods.goodsArr[0]
		this.showShopCar = this.showShopCar.bind(this);
		this.addchooseNum = this.addchooseNum.bind(this);
		this.decchooseNum = this.decchooseNum.bind(this);
		this.gopage = this.gopage.bind(this);
		this.delgoods = this.delgoods.bind(this);
		
	}
	gopage(){
		store.dispatch({
			type:'shopcar',
			shopnumId:store.getState().shopNum,
		})
	}
	showShopCar(e){
		this.setState({
			carshow:!(this.state.carshow)
		})
		
	}
	addchooseNum(e){
		this.setState({
			chooseNum:++this.state.buyCarArr[e.target.dataset.index].chooseNum,
		})	
		store.dispatch({
			type:'shopPage',
			goodsNum:this.state.goodsNum,
			chooseNum: this.state.chooseNum,
			goodslist:this.state.buyCarArr,
			shopnumId:store.getState().shopNum,
			
		})
//		console.log(this.state.buyCarArr)
	}
	decchooseNum(e){
		this.setState({
			chooseNum:--this.state.buyCarArr[e.target.dataset.index].chooseNum,
		})
		if(this.state.chooseNum == 0){
			console.log("dec")
			this.state.buyCarArr = '';
			data.buyCar.chooseGoods = '';
		}
		store.dispatch({
			type:'shopPage',
			goodsNum:this.state.goodsNum,
			chooseNum : this.state.chooseNum,
			goodslist:this.state.buyCarArr,
			totalprice:this.state.totalprice,
			shopnumId:store.getState().shopNum,
		})
//		console.log(this.state.buyCarArr)
		
	}
	delgoods(){
//		this.state.carshow = false;
		this.forceUpdate();
//		清空店里选择的商品
		var arr = this.state.showObj.showPage.goods.goodsArr;
		for(let i=0;i<arr.length;i++){
			for(let j=0; j<arr[i].length;j++){
				arr[i][j].chooseNum=0
			}
		}
		console.log(arr)
		this.state.buyCarArr = '';
		data.buyCar.chooseGoods = '';
		this.forceUpdate();
		store.dispatch({
			type:'shopPage',
			goodsNum:this.state.goodsNum,
			chooseNum : this.state.chooseNum,
			goodslist:this.state.buyCarArr,
			totalprice:this.state.totalprice,
			shopnumId:store.getState().shopNum,
		})
	
	}
	
	
	render(){
		var arr=this.state.showObj.showPage.goods.goodsArr;
		var newArr = [];
		for(let i=0;i<arr.length;i++){
			for(let j=0; j<arr[i].length;j++){
				if(arr[i][j].chooseNum>0){
					newArr.push(arr[i][j]);
				
				}
			}
		}
		
		this.state.total = 0;
		this.state.totalricecount=0;
		this.state.buyCarArr = newArr;
//		console.log(this.state.buyCarArr)
		if(this.state.buyCarArr !=''){
			data.buyCar.chooseGoods=this.state.buyCarArr;
			data.buyCar.chooseGoods.shopname= this.state.showObj.name;
		}

		for(let i=0;i<arr.length;i++){
			for(let j=0; j<arr[i].length;j++){
				
				this.state.totalricecount+=arr[i][j].chooseNum;
				this.state.total+=(arr[i][j].chooseNum*arr[i][j].goodsPrice ); 
				
				this.state.lowpric = this.state.showObj.startPrice - this.state.total;
				console.log(this.state.total)
//				data.buyCar.chooseGoods.totalprice = parseInt(this.state.total);
				
			}
			
		}
		
		
		
		
		return (
			<div className="ShopCar">
			<div className="shopCar" onClick={this.showShopCar}>
			
				<div className="car">
					<div className="totalcount">{this.state.totalricecount}</div>
					<span className="shopcar iconfont icon-gouwuche" ></span>	
				</div>
				<div>
					<span>¥{parseInt(this.state.total + this.state.totalricecount)}</span>
					<span>配送费¥5</span>
				</div>
				
				<div className="decPrice">
					<span onClick={this.gopage}>{(this.state.total)?(this.state.total)>=this.state.showObj.startPrice ?'去结算':'还差'+this.state.lowpric:this.state.showObj.startPrice+'元起送'}</span>
				</div>
			</div >
			{/*菜单列表*/}
			{this.state.carshow?
				<div className="disableCar">
				<div className="shopdetail">
				<div className="carlist">
							
							<div className="cartitle">
								<span>以满75,结算减10元</span>
							</div>
							<div className="optiongoods">
								<div>
									<span>|</span>
									<span>已选商品</span>
								</div>
								<div>
									<span>|</span>
									<span onClick={this.delgoods}>清空</span>
								</div>
							</div>
				{
					this.state.buyCarArr.length?this.state.buyCarArr.map(function(item,index,arr){
						return (
							<div className="opgoodslist" key={index}>
								<div>{item.name}</div>
								<div>
									<span>¥{parseInt(item.goodsPrice*item.chooseNum)}</span>
									<span className="opt iconfont icon-jian" onClick={this.decchooseNum} data-index={index}></span>
									<span>{item.chooseNum}</span>
									<span className="opt iconfont icon-jia" onClick={this.addchooseNum} data-index={index}></span>
								</div>
							</div>
							
						  
						)
					},this):null
				}
					<div className="ricebox">
						<span>餐盒</span>
						<span>{this.state.totalricecount}</span>
					</div>
				</div>
				</div>
			</div>
			:null}
			
		</div>)
	}
}


export default ShopPageBuyCar;



