import React,{Component} from 'react';
import store from '../store.js';


class Shop extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	
	render(){
		return (<div className="shop">
			<div className="shoptitle">
				<div>活动与服务</div>
			 	<ul>
			 		<li><span className="iconfont icon-te"></span><span>满40减15，满80减40</span></li>
			 		<li><span className="iconfont icon-shou"></span><span>新用户下单立减17元(不与其它活动同享)</span></li>
			 		<li><span className="iconfont icon-te"></span><span>爆款椒盐濑尿虾18元</span></li>
			 		<li><span className="iconfont icon-te"></span><span>30元【生猛大闸蟹3只】</span></li>
			 		<li><span className="iconfont icon-te"></span><span>太平馆西餐厅9.9元</span></li>
			 		<li><span className="iconfont icon-te"></span><span>太平馆爆款套餐15元</span></li>
			 		<li><span className="iconfont icon-te"></span><span>太平馆爆款5折！</span></li>
			 		<li><span className="iconfont icon-te"></span><span>已加入“外卖保”计划，食品安全有保障</span></li>
			 	</ul>
			</div>
			<div className="shopcontent">
				<div><p>商家信息</p><p>太平洋西餐厅</p></div>
				<div>
					<span>商家电话</span>
					<span>020-833211305{">"}</span>
				</div>
				<div>
					<span>地址</span>
					<span>广州市越秀区北京路342号首层、阁楼、二楼</span>
				</div>
				<div>
					<span>营业时间</span>
					<span>11:00-21:00</span>
				</div>
			</div>
			<div className="shopbottom">
				<span>营业资质</span>
				<span>{'>'}</span>
			</div>
		</div>)
	}
}


export default Shop;



