import React,{Component} from 'react';
import store from './store.js';
import data from './data.js'


class ListPage extends Component{
	constructor(){
		super();
		this.state={
			showObj:data.listPage,
			issort:false,
			shopNum:0,
		}
		this.sort = this.sort.bind(this);
		this.goPage = this.goPage.bind(this);
		this.sendsort = this.sendsort.bind(this);
		this.sendmoneysort = this.sendmoneysort.bind(this);
		this.salecount = this.salecount.bind(this);
		this.startsend = this.startsend.bind(this);
		
		
	}
	goPage(e){
		store.dispatch({
			type:'shopPage',
			shopnumId:e.currentTarget.dataset.num
		})
		
		console.log(e.currentTarget.dataset.num)
		this.state.shopNum=e.currentTarget.dataset.num;
		this.forceUpdate();
	}
	backhomepage(e){
		store.dispatch({
			type:'index',
		})
	}
	sort(e){
		this.state.issort=!this.state.issort;
		this.forceUpdate();
		console.log(this.props.obj);
		
		
	}
//	配送最快
	sendsort(e){
		this.state.issort=false;
		this.forceUpdate();
		this.state.showObj.meishi.sort(function(a,b){
			if(a.arrivetime == b.arrivetime){
				return a.star < b.start
			}
			return a.arrivetime > b.arrivetime
		})
		
	}
//	起送费低
	sendmoneysort(e){
		this.state.issort=false;
		this.forceUpdate();
		this.state.showObj.meishi.sort(function(a,b){
			if(a.startPrice == b.startPrice){
				return a.star < b.start;
			}
			return a.startPrice > b.startPrice;
		})
	}
//	销量最高
	salecount(e){
		this.state.issort=false;
		this.forceUpdate();
		this.state.showObj.meishi.sort(function(a,b){
			if(a.salesVolumn == b.salesVolumn){
				return a.star < b.start;
			}
			return a.salesVolumn < b.salesVolumn;
		})
	}
//	评价最高
	startsend(e){
		this.state.issort=false;
		this.forceUpdate();
		this.state.showObj.meishi.sort(function(a,b){
			if(a.star == b.star){
				return a.salesVolumn < b.salesVolumn;
			}
			return a.star < b.star;
		})
	}
	
	render(e){
		return (<div >
				<div className="listpagetitle">
					<span onClick = {this.backhomepage}>{'<'}</span>
					<span>美食</span>
				</div>
				<div className="listpageNav">
					<span>分类</span>
					<span onClick={this.sort}>排序</span>
					<span>筛选</span>
					{this.state.issort?<div className="sort" >
						<span onClick={this.sendsort}>配送最快</span>
					    <span onClick={this.sendmoneysort}>起送费低</span>
						<span onClick={this.salecount}>销量最高</span>
						<span onClick={this.startsend}>评价最高</span>
					</div>:null}
					
				</div>
			{this.state.showObj.meishi.map(function(item,index,arr){
				return (
					<div className="listshop" onClick={this.goPage} data-num={index} key={index}>
					  <div>
						<div className="shopimg"><img src={item.showImg}/></div>
						
						<div className="listshoptop">
							<div className="shopname">
								<div>
									<div className="brand">品牌</div>
									<span>{item.name}</span>
								</div>
								<div className="align">
									<span>保</span>
									<span>票</span>
								</div>
							</div>
							<div className="shopcomment">
								<div>{item.star} 月售{item.salesVolumn}单</div>
								<div>{item.send}</div>
							</div>
							<div className="shopdetail">
								<div>
									<span>¥{item.startPrice}起送</span>
									<span>|</span>
									<span>配送费¥{item.sendPrice}</span>
								</div>
								<div>
									<span>1.10km</span>
									<span>|</span>
									<span>{item.arrivetime}分钟</span>
								</div>
							</div>
							<div className="shopgood">
								<span></span>
								<span>{item.comment}</span>
							</div>
							<div className="line"></div>
							<div className="listshopbottom">
								<div >
									<span></span>
									<span>{item.discount}</span>
								</div>
								<div >
									<span></span>
									<span>{item.firstdiscount}</span>
								</div>
							</div>
						
						</div>
						
						
					</div>
				</div>
				)
			},this)}
				
				
		</div>)
	}
}


export default ListPage;


