import React,{Component} from 'react';
import store from './store.js';


class Myselfpage extends Component{
	constructor(){
		super();
		this.goPage = this.goPage.bind(this);
		console.log(store.getState().shopNum)
	}
	goPage(){
		store.dispatch({
			type:'shopPage',
			shopnumId:store.getState().shopNum,
		})
	}
	
	render(){
		return (<div className='myselfpage'>
					<div className="myselftitle">
						<span >{'<'}</span>
						<span>我的</span>
					</div>	
					<div className = "myselftitle1">
						<div>
							<div className = "userimg"> <img /> </div>
							<div className= "userinfo">
								<p className="username">111aaa</p>
								<div className="phone">
									<span>0</span>
									<span>151****7217</span>
								</div>
							</div>
						</div>
						<div className="gopage">{">"}</div>
					</div>
					
					<div className="myselfbody">
						<div className="mybl">
							<p>0 <span>个</span></p>
							<p>优惠</p>
						</div>
						<div className="mybr">
							<p>0 <span>个</span></p>
							<p>金币</p>
						</div>
					</div>
					
					<div className="myselfpos">
						<div>
							<span></span>
							<span>我的地址</span>
						</div>
						<div>{">"}</div>
					</div>
					
					<div className = "myselfward">
						<div>
							<div>
								<span></span>
								<span>金币商城</span>
							</div>
							<div>{">"}</div>
						</div>
						<div>
							<div>
								<span></span>
								<span>分享拿5元现金</span>
							</div>
							<div>{">"}</div>
						</div>
					</div>
					
					<div className ="myselfbottom">
						<div>
							<div>
								<span></span>
								<span>我的客服</span>
							</div>
							<div>{">"}</div>
						</div>
						<div>
							<div>
								<span></span>
								<span>下载饿了么APP</span>
							</div>
							<div>{">"}</div>
						</div>
					</div>
			</div>)
	}
}


export default Myselfpage;



