import React,{Component} from 'react';
import data1 from './data1.js';
import store from './store.js'



class TestCom2 extends Component{
	constructor(){
		super();
		this.add = this.add.bind(this);
	}
	add(e){
		
		++data1.num;
		this.forceUpdate()
		console.log(data1.num)
		
	}
	render(){
		return (<div>
			<button onClick={this.add}>父级组件</button>
			<h1>{data1.num}</h1>
			<Child1  />
			<Child3  />
			<Child2  action={this.add} />
		</div>)
	}
}
class Child1 extends Component{
	constructor(){
		super()
		this.add = this.add.bind(this)
	}
	add(e){
		data1.num++;
		store.dispatch({
			type:"testcom"
		})
	}
	render(){
		return (<div>
			<h1>子组件1:{data1.num}</h1>
		<button onClick={this.add}>++</button>
		</div>)
	}
}


class Child2 extends Component{
	constructor(){
		super()
	}

	render(){
		return (<div>
			<h1>子组件2:{data1.num}</h1>
			<button onClick={this.props.action}>++</button>
			
		</div>)
	}
}

class Child3 extends Component{
	constructor(){
		super()
		this.state={
			num:data1.num
		}
		this.add = this.add.bind(this)
	}
	add(e){
		data1.num++;
	}
	render(){
		this.state.num = data1.num
		return (<div>
			<h1>子组件1:{this.state.num}</h1>
		<button onClick={this.add}>++</button>
		</div>)
	}
}

export default TestCom2;



