import React,{Component} from 'react';
import store from './store.js';
import ShopPageTop from './shopPage/shopPageTop.js';
import ShopPageMain from './shopPage/shopPageMain.js';
import ShopPageBuyCar from './shopPage/shopPageBuyCar.js';


class ShopPage extends Component{
	constructor(){
		super();
		console.log(store.getState().totalprice)
		
	
	}
	goPage(){
		
		store.dispatch({
			type:'listPage',
			goodslist:store.getState().goodsList,
			totalprice:store.getState().totalprice,
		})
	}
	
	render(){
		console.log(store.getState().totalprice)
		return (
			<div>
				<ShopPageTop obj={store.getState().totalprice}/>
				<ShopPageMain obj={store.getState()} />
			</div>
		)
	}
}


export default ShopPage;


