import React,{Component} from 'react';
import store from '../store.js';
import data from '../data.js'


class ShopPageTop extends Component{
	constructor(){
		super();
		this.state={
			showObj:data.listPage.meishi[store.getState().shopNum]
		}
//		console.log("shopnum"+store.getState().shopNum);
		console.log(store.getState().shopNum)
		this.shopcarpage = this.shopcarpage.bind(this);
//		console.log(store.getState().goodsList)
	}
	goPage(e){
		console.log(store.getState().shopNum);
		store.dispatch({
			type:'listPage',
			shopnumId:e.currentTarget.dataset.num
		})
	}
	shopcarpage(e){
//		console.log("shopcar")
		store.dispatch({
			type:'shopcar',
			shopnumId:store.getState().shopNum,
			goodslist:store.getState().goodsList,
			totalprice:store.getState().totalprice,
		})
	}
	
	render(){
//			console.log(this.state.showObj.showImg);
		return (
			
				<div className='shopPageTopmain' style={{backgroundImage: 'url('+this.state.showObj.backgroundimg+')'}}>
					<div className='shopPageTop' >
						<h1 className='shopPageTopNav'>
							<span className='back' onClick={this.goPage}>{'<'}</span>
							<span  className='iconfont icon-gouwuche' onClick={this.shopcarpage}></span>
						</h1>
						
						<div className="shopPageTitle">
							<img src={this.state.showObj.showImg} />
							<div>
								<p>{this.state.showObj.name}</p>
								<p>{this.state.showObj.send}/{this.state.showObj.arrivetime}到达/配送费¥{this.state.showObj.sendPrice}</p>
								<p className="tell">公告：亲爱的小主，高峰期点餐会被平台加收配送费，为了您的利益考虑，可以尽早点餐。单点单品、饮料等不配送哦，望朋友们理解。</p>
							</div>
						</div>
						
						<div className="shopPageBottom">
							<div>
								<img src={require('../../img/jian.png')} />
								<span>满20减19，满45减21，满60减23，满80减25</span>
							</div>
							<span>3个活动</span>
						</div>
					
					
					</div>
				</div>
		)
	}
}


export default ShopPageTop;



