import React,{Component} from 'react';



class TestCom extends Component{
	constructor(){
		super();
		this.state ={
			num:0
		}
		this.add = this.add.bind(this);
	}
	add(e){
//		this.setState({
//			num:++this.state.num
//		})
		this.state.num++;
		this.forceUpdate()
	}

	render(){
		return (<div>
			<button onClick={this.add}>父级组件</button>
			<h1>{this.state.num}</h1>
			<Child1 num={this.state.num} action={this.add}/>
			<Child2 num={this.state.num} action={this.add} />
		</div>)
	}
}
class Child1 extends Component{
	constructor(){
		super()
//		this.add = this.add.bind(this)
	}
//	add(){
//		this.props.num++;
//	}
	render(){
		return (<div>
			<h1>子组件1:{this.props.num}</h1>
		<button onClick={this.props.action}>++</button>
		</div>)
	}
}
class Child2 extends Component{
	constructor(){
		super()
	}

	render(){
		return (<div>
			
			<h1>子组件2:{this.props.num}</h1>
			<button onClick={this.props.action}>++</button>
			
		</div>)
	}
}


export default TestCom;



