import {createStore} from 'redux';


var reducer = function(state,action){
	
	var newState = {
		
		viewCom:'index',
		
	};
	
	if(action.type){
		newState.viewCom = action.type;
		newState.shopNum = action.shopnumId;
		newState.goodsNum = action.goodsNum;
		newState.chooseNum = action.chooseNum;
		newState.goodsList = action.goodslist;
		newState.totalprice = action.totalprice;
	}
	return newState;
}
var store = createStore(reducer);

export default store;



