import React,{Component} from 'react';
import store from '../store.js';
import data from '../data.js'


class ShopCar extends Component{
	constructor(){
		super();
			console.log(store.getState().shopNum);
//			console.log(store.getState().goodsList);
			console.log(data.buyCar.chooseGoods)
//			console.log(store.getState().totalprice)
			this.state={
				shopnum:store.getState().shopNum,
				total:0,
				
			}
//			console.log(data.buyCar.chooseGoods)
			this.backshop = this.backshop.bind(this);
			this.gopage = this.gopage.bind(this)
			this.delgoods = this.delgoods.bind(this)
			this.delshopall = this.delshopall.bind(this)
		
	}
	backshop(e){
		
		store.dispatch({
			type:'shopPage',
			shopnumId:store.getState().shopNum,
			goodslist:store.getState().goodsList,
		})
		console.log(store.getState().shopNum)
//		console.log(this.props.obj)
	
	}
	gopage(){
		store.dispatch({
			type:'order',
			shopnumId:store.getState().shopNum,
		})
	}
	delshopall(){
//		清空店里选择的商品
		var arr = data.buyCar.chooseGoods;
		for(let i=0;i<arr.length;i++){
				arr[i].chooseNum=0
		}
		console.log(arr);
		data.buyCar.chooseGoods='';
		
//		this.state.buyCarArr = '';
//		data.buyCar.chooseGoods = '';
//		this.forceUpdate();
		store.dispatch({
			type:'shopcar',
			goodsNum:this.state.goodsNum,
			chooseNum : this.state.chooseNum,
			goodslist:this.state.buyCarArr,
			totalprice:this.state.totalprice,
			shopnumId:store.getState().shopNum,
		})
	
	}
	delgoods(e){
//		清空店里选择的商品
//		var arr = data.buyCar.chooseGoods[e.currentTarget.dataset.num];
		data.buyCar.chooseGoods[e.currentTarget.dataset.num] = '';
		console.log(data.buyCar.chooseGoods);
		console.log(e.currentTarget.dataset.num)
		console.log(data.buyCar.chooseGoods[e.currentTarget.dataset.num])
		
//		this.state.buyCarArr = '';
//		data.buyCar.chooseGoods = '';
//		this.forceUpdate();
		store.dispatch({
			type:'shopcar',
			goodsNum:this.state.goodsNum,
			chooseNum : this.state.chooseNum,
			goodslist:this.state.buyCarArr,
			totalprice:this.state.totalprice,
			shopnumId:store.getState().shopNum,
		})
	
	}
	
	render(){
		
		var arr=data.buyCar.chooseGoods;
		console.log(arr)
		for(let i=0;i<arr.length;i++){
			console.log(arr.length)
				this.state.total+=arr[i].chooseNum*arr[i].goodsPrice;
				data.buyCar.chooseGoods.totalprice = parseInt(this.state.total);
				console.log(this.state.total);
		}

		return (<div className="shopcarpage" > 
			<div className="shopcarl">
			
				<div className="shopcartitle">
					<span onClick={this.backshop}>{'<'}</span>
					<span>购物车</span>
				</div>
				<div className="titleover"></div>
				{
					data.buyCar.chooseGoods.length?
						<div className="sc">
					<div className="shopcarmain">
						<div className="sc-shopname">
							<span>{data.buyCar.chooseGoods.shopname}{'>'}</span>
							<span onClick={this.delshopall}>{'×'}</span>
						</div>
							{
							data.buyCar.chooseGoods.length?data.buyCar.chooseGoods.map(function(item,index,arr){
									return (<div>
										
										<ul className="sc-shoplist" >
											<li >
												<div><img className="shopcarimg" src={'//fuss10.elemecdn.com/f/a1/f4ec123b8ceaa28e48825e2af9a26jpeg.jpeg?imageMogr/format/webp/'} /></div>
												<div className="sc-goodsname" >
													<div className="sr-gname" >
														<span>{item.name}</span>
														<span onClick={this.delgoods}>{'×'}</span>
													</div>
													<div className="shoplistdetail">
														<span>{'×'}{item.chooseNum}</span>
														<span>¥{item.goodsPrice}</span>
													</div>
												</div>
											</li>
											
										</ul>
										
											</div>)
									},this):<div>123</div>
						
							}
							<div className="shopcarpay">
								<span>合计</span>
								<span>¥{data.buyCar.chooseGoods.totalprice}</span>
								<span onClick={this.gopage}>去结算</span>
							</div>
							
						
					</div>
					
					</div>
					:<div className="carnull">
						<img src={'//fuss10.elemecdn.com/2/50/8019fbaebac2aaa76e2d9b5e5b407gif.gif'}/>
						<h3>您的购物车为空</h3>
					</div>
				}
				
			</div>
		</div>)
	}
}


export default ShopCar;



