import React,{Component} from 'react';
import store from '../store.js';
import data from '../data.js';
import ShopPageBuyCar from './shopPageBuyCar.js';


class Goods extends Component{
	constructor(){
		super()
		this.state = {
			showObj:data.listPage.meishi[store.getState().shopNum],
            goodsNum:0, 
            current:0,
		}
		this.state.goodsType=this.state.showObj.showPage.goods.goodsType;
		this.state.goodsArr = this.state.showObj.showPage.goods.goodsArr[0];
		this.state.chooseNum = this.state.showObj.chooseNum;
		this.goodsTypeClick = this.goodsTypeClick.bind(this);
		this.addGoodsClick = this.addGoodsClick.bind(this);
		this.decGoodsClick = this.decGoodsClick.bind(this);
		this.state.goodsNum = 0;
	}
	goodsTypeClick(e){
//		console.log(e.target.dataset.num+"e.target.dataset.num")
		//tab切换样式
//		e.target.parent.style.color="black"
//		e.target.style.color="red"

		this.setState({
			goodsArr:this.state.showObj.showPage.goods.goodsArr[e.target.dataset.num],
			goodsNum:e.target.dataset.num,
			current:e.target.dataset.num,
	
		})
		store.dispatch({
			type:'shopPage',
			numId:store.getState().shopNum,
			goodsNum:e.target.dataset.num,
			shopnumId:store.getState().shopNum,
			chooseNum : this.state.chooseNum,
		})

	}
	addGoodsClick(e){
		//函数，里面包含对象
		this.setState({
			chooseNum:++this.state.goodsArr[e.target.dataset.index].chooseNum,
			
		})
//		console.log(store.getState())
		store.dispatch({
			type:'shopPage',
			goodsNum:this.state.goodsNum,
			shopnumId:store.getState().shopNum,
			chooseNum : this.state.chooseNum,
		})
		
	}
	decGoodsClick(e){
		this.setState({
			chooseNum:--this.state.goodsArr[e.target.dataset.index].chooseNum,
		})
		if(this.state.chooseNum<0){
//			console.log(this.state.chooseNum)
			this.setState({
				chooseNum:0,
			})
		}
		store.dispatch({
			type:'shopPage',
			goodsNum:this.state.goodsNum,
			shopnumId:store.getState().shopNum,
			chooseNum : this.state.chooseNum,
		})
		console.log(e.target.dataset.index)
		
	}
	render(){

		console.log('del')
		var idx= this.state.current;
//this.state.goodsArr = data.listPage.meishi[0].showPage.goods.goodsArr[0];
		return (
			<div className="goods">
				<div className='typeList'>
				
					{
						this.state.goodsType.map(function(item,index,arr){
							return (
									<div onClick={this.goodsTypeClick} key={index} data-num={index} className='typeListItem' className={ idx == index?'active':''}>{item}</div>
									)
						},this)
						
					}
				
				</div>
				<div className='goodsList'>
				
					{
						this.state.goodsArr.map(function(item,index,arr){
							return (<div key={index}>
								<img src={item.goodsImg}/>
									<div>
										<p>{item.name}</p>
										<p>{item.chief}</p>
										<p>
											<span>月售{item.salesVolumn}份</span>
											<span>好评率{item.goodsComNum}%</span>
										</p>
										<p>
											<span className="price">¥{item.goodsPrice}</span>
											<div>
												
													<span onClick={this.decGoodsClick} data-index={index} className="opt iconfont icon-jian"></span>
													<span style={{marginTop:'20px;'}}>{item.chooseNum}</span>
													<span onClick={this.addGoodsClick} data-index={index} className="opt iconfont icon-jia"></span>
											
												
											</div>
										</p>
									</div>		
							</div>)
						},this)
						
					}
				
				
				</div>
				<ShopPageBuyCar obj={store.getState()} addclick={this.addGoodsClick}  decclick={this.decGoodsClick} />
			</div>
		)
		
	}
}


export default Goods;



