import React from 'react';
import ReactDOM from 'react-dom';
import './css/index.css';
import './css/listpage.css';
import './css/iconfont.css';
import './css/homepage.css';
import './css/shopcar.css';
import './css/myself.css';
import './css/orderpage.css';
import './css/findpage.css';
import './css/com.css';
import './css/shop.css';
import store from './js/store.js';

import HomePage from './js/HomePage.js'
import ListPage from './js/ListPage.js'
import ShopPage from './js/ShopPage.js'
import ShopCar from './js/shopPage/shopcar.js'
import ShopPageBuyCar from './js/shopPage/shopPageBuyCar.js'
import Order from './js/shopPage/order.js'
import Takeoutfood from './js/takeoutfood.js'

import TestCom from './js/testCom.js'
import TestCom2 from './js/testCom2.js'
import TestCom3 from './js/testCom3.js'

var viewComName,viewCom=<HomePage  />;

var togglePage = function(){
//	console.log(store.getState());
	viewComName=store.getState().viewCom;
	switch(viewComName){
		case 'index':
		 	viewCom = <HomePage obj={store.getState()}/>
		 	break;
		case 'listPage':
		 	viewCom = <ListPage   obj={store.getState()}/>
		 	break;
		case 'shopPage':
		 	viewCom = <ShopPage  obj={store.getState()}/>
		 	break;
		case 'shopcar':
		 	viewCom = <ShopCar obj={store.getState()}/>
		 	break;
		case 'testcom':
		 	viewCom = <TestCom2 obj={store.getState()}/>
		 	break;
		case 'order':
		 	viewCom = <Order obj={store.getState()}/>
		 	break;
		case 'takeoutfood':
		 	viewCom = <Takeoutfood obj={store.getState()}/>
		 	break;
		default:
			viewCom = <HomePage  obj={store.getState()}/>
		 	break;
		 
	}
}

store.subscribe(function(){
	togglePage();
	ReactDOM.render( viewCom,document.getElementById('root'))
})
ReactDOM.render(viewCom,document.getElementById('root'))
//ReactDOM.render(<TestCom2 />,document.getElementById('root'))



