import React,{Component} from 'react';
import store from './store.js';
import data from './data.js'
import Takeoutfood from './takeoutfood.js'
import Findpage from './findpage.js'
import Orderpage from './Orderpage.js'
import Myselfpage from './myselfpage.js'

class HomePage extends Component {
	constructor(){
		super();
		this.state={
			showChildTab:<Takeoutfood obj={store.getState()} />,
			showObj:data.listPage,
			current:0,
		}
		this.tabPage = this.tabPage.bind(this)
	}
	tabPage(e){
		this.setState({
			current:e.target.dataset.num,
		})
		console.log(e.target.dataset.num)
	switch(e.target.dataset.num){
		case '0':
			this.setState({
				showChildTab:<Takeoutfood obj={store.getState()} />
			});
			break;
		case '1':
			this.setState({
				showChildTab:<Findpage  />
			});
			break;
		case '2':
			this.setState({
				showChildTab:<Orderpage />
			});
			break;
		case '3':
			this.setState({
				showChildTab:<Myselfpage />
			});
			break;
		}
	}
	render(){
		var index = this.state.current;
		return (
			<div className="homepage"  >
				{this.state.showChildTab}

				<div className="bottomnav">
						<ul>
							<li >
								<span className="iconfont icon-waimai" ></span>
								<span className={index == 0?'homeactive':''} data-num='0' onClick={this.tabPage}>外卖</span>
							</li>
							<li >
								<span className="iconfont icon-fasong"></span>
								<span className={index == 1?'homeactive':''} data-num='1' onClick={this.tabPage}>发现</span>
							</li>
							<li >
								<span className="iconfont icon-dingdan1"></span>
								<span className={index == 2?'homeactive':''} data-num='2' onClick={this.tabPage}>订单</span>
							</li>
							<li >
								<span className="iconfont icon-home"></span>
								<span className={index == 3?'homeactive':''} data-num='3' onClick={this.tabPage}>我的</span>
							</li>
						</ul>
					</div>
			</div>
		)
	}
}

export default HomePage;


